import { Text as RNText, StyleSheet } from 'react-native'

import type { Props } from './interfaces'

const styles = StyleSheet.create({
  baseText: {
    fontFamily: 'Cochin',
  },
  titleText: {
    fontSize: 20,
    fontWeight: 'bold',
  },
})
const Text = ({ style, title, bodyText }: Props) => (
  <RNText style={[styles.baseText, style]}>
    <RNText style={styles.titleText}>
      {title}
    </RNText>
    <RNText numberOfLines={5}>{bodyText}</RNText>
  </RNText>
)

export default Text